// 导航栏
.my-navagator {
    width: 100%;
    line-height: 48px;
    background-color: #006d46;
    padding-left: 10%;
    margin: 0;
    li {
        width: 10%;
        float: left;
        text-align: center;
        overflow: hidden;
        &:hover {
            cursor: pointer;
            background-color: rgba(1, 70, 45, .7);
        }
        a {
            display: inline-block;
            width: 100%;
            font-size: 16px;
            color: #fff;
            text-decoration: none;
            padding: 0;
        }
    }
}
